.nav {
  text-transform: uppercase;
  position: absolute;
  bottom: 25px;
  left: 50%;
  transform: translate(-50%);
  width: 100%;
  max-width: 950px;

  &__list {
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 970px) {
      visibility: hidden;
      flex-direction: column;
      align-items: flex-start;
    }

    &--link {
      display: none;
    }

    &--item {
      padding: 7px 1vw;
      margin: 0 1vw;
      list-style-type: none;

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    &--active {
      background-color: #182f33;
      border-radius: 10px;
    }

    &-link {
      display: block;
      color: white;
      text-decoration: none;
      transition: color $hover-duration;

      &:hover {
        color: $active-color;
      }

      &:focus {
        outline: none;
        color: #1fffff;
      }
    }
  }
}

.menu__toggle {
  visibility: hidden;
}

@media screen and (max-width: 970px) {

  .menu__toggle {
    opacity: 0;
    position: absolute;
    top: 95%;
  }

  .menu__toggle:checked~.menu__btn>.menu__span {
    transform: rotate(45deg);
  }

  .menu__toggle:checked~.menu__btn>.menu__span::before {
    top: 0;
    transform: rotate(0);
  }

  .menu__toggle:checked~.menu__btn>.menu__span::after {
    top: 0;
    transform: rotate(90deg);
  }

  .menu__toggle:checked~.nav__list {
    top: 40px;
  }

  .menu__btn {
    display: block;
    position: relative;
    margin: 20px auto 0;
    top: 42px;
    right: -75vw;

    width: 40px;
    height: 30px;

    cursor: pointer;
    z-index: 1;
  }

  .menu__span::before,
  .menu__span,
  .menu__span::after {
    display: block;
    position: absolute;

    width: 100%;
    height: 4px;

    background-color: white;

    transition-duration: 0.3s;
  }

  .menu__span::before {
    content: '';
    top: -8px;
  }

  .menu__span::after {
    content: '';
    top: 8px;
  }
}

@media screen and (max-width: 970px) {
  .nav__list-show {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    font-size: 22px;
    line-height: 22px;
    margin: 0;
    position: absolute;
    top: -543px;
    width: 100%;
    height: 568px;
    visibility: visible;
    background-color: $main-focus-color;
    background-image: url(../images/logo.png);
    background-repeat: no-repeat;
    background-position-y: 35px;
    background-position-x: 37vw;
    background-size: 75px;
  }

  .nav {
    &__list {
      &--item {
        margin-bottom: 32px;
        margin-left: 15vw;
        padding: 0;
      }

      &--link {
        display: block;
        margin-bottom: 60px;
        margin-left: 15vw;
        font-size: 12px;
        line-height: 16px;
      }

      &--active {
        background-color: transparent;
        padding-left: 15vw;
      }
    }
  }
}
