@import "utils";

.header {
  height: 700px;
  background-image: url("../images/header.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
  text-transform: uppercase;
  font-weight: 400;

  &__wrap {
    display: flex;
  }

  &__top {
    position: absolute;
    display: flex;
    justify-content: space-between;
    top: 35px;
    left: 50%;
    transform: translate(-50%);
    width: 80vw;
    max-width: 1020px;
    height: 160px;
  }

  &__logo {
    width: 115px;
    height: 76px;

    &-img {
      width: 100%;
    }
  }

  &__phone,
  &__link {
    position: relative;
    top: 56px;
  }

  &__phone {
    color: white;
    letter-spacing: 2px;

    &--logo {
      position: absolute;
      top: 5px;
      left: 3vw;
      margin: 15px;
      padding: 15px;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url(../images/phone.svg);
      display: none;

      @media screen and (max-width: 970px) {
        display: block;
      }
    }

    &-link {
      text-decoration: none;
      color: white;
      transition: color 0.3s;

      &:focus {
        outline: none;
        color: #1fffff;
      }

      &:hover {
        color: $active-color;
      }
    }
  }

  &__link {
    text-decoration: none;
    color: white;
    border-bottom: 1px solid;
    letter-spacing: 2px;
    transition: color 0.3s;

    &:focus {
      outline: none;
      color: #1fffff;
    }

    &:hover {
      color: $active-color;
    }
  }

  &__title {
    position: absolute;
    top: 414px;
    left: 50%;
    transform: translate(-50%);
    width: 77vw;
    max-width: 1020px;
    height: 160px;
    color: white;
    font-weight: bold;
    font-size: 60px;
    line-height: 60px;
    text-transform: none;
    letter-spacing: -1.5px;
    text-align: start;

    &::before {
      content: "";
      position: absolute;
      height: 2px;
      width: 35px;
      background-color: white;
      margin-top: -10px;
    }
  }
}

@media screen and (max-width: 970px) {
  .header {
    height: 568px;

    &__title {
      font-size: 46px;
      line-height: 40px;
    }

    &__phone,
    &__link {
      visibility: hidden;
    }
  }
}
