@import "utils";

.footer {
  background-image: url("../images/footer.png");
  height: 359px;
  background-size: cover;
  background-position: fixed;
  position: relative;
  bottom: 0;
  z-index: -2;
  margin-top: 88px;

  &__logo {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    bottom: 40px;
  }
}

@media screen and (max-width: 970px) {
  .footer {
    @include mobile-alignment(64);
    height: 206px;

    &__logo {
      &__img {
        min-width: 77px;
        width: 15vw;
      }
    }
  }
}
