@import "utils";

.testimonials {
  @include alignment(152);

  &__title {
    @include toTitle;
  }

  &__crown {
    @include toCrown;
  }

  &__wrap {
    display: flex;
    justify-content: center;
  }
  &__person {
    width: 240px;
    height: 363px;
    margin-top: 70px;
    margin-right: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;

    &:hover img {
      transform: scale(1.25);
      border-radius: 20%;
      box-shadow: 0 6px 23px rgba(0, 117, 255, 0.46);
    }

    &:last-child {
      margin-right: 0;
    }

    &-photo {
      display: block;
      border-radius: 50%;
      margin-bottom: 28px;
      transition: transform 0.5s, border-radius 0.5s, box-shadow 0.5s;

    }
    &-name {
      font-weight: bold;
      font-size: 24px;
      color: $main-text-color;
      margin-bottom: 4px;
    }
    &-position {
      color: #9fadc2;
    }
    &-phrase {
      margin-top: 13px;
      text-align: center;
      font-size: 16px;
      line-height: 27px;
      color: $text-color;
    }
  }
}

@media screen and (max-width: 970px) {
  .testimonials {
    @include mobile-alignment(64);

    &__wrap {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-top: 53px;
    }
    &__person {
      margin: 15px auto;
      &:last-child {
        margin: 15px auto;
      }
      &:first-child {
        height: 320px;
      }
    }
  }
}
